import React from "react";
import { useParams } from "react-router-dom";
import { CodeEditor, FullEditor } from "components/ui/Editor/Editor";
import { Page, PageContentBase } from "components/pages/Page";
import { Col } from "antd";
import { useQuery } from "react-query3";
import axios from "axios";
import { SchemaName } from "components/ui/Form/types";

export default function ConfigurationPage() {
  const { name } = useParams();
  const { data: fileContent } = useQuery<string>(
    `/settings/configuration/${name}`,
    async () => await (await axios.get(`/api/v1/configuration/${name}`)).data,
    {
      enabled: !!name,
    }
  );

  return (
    <Page
      resource={{
        content: fileContent,
        resourceInfo: {
          parent: `/configuration/${name}`,
          self: `/configuration/${name}`,
          schemaName: "configuration" as SchemaName,
        },
      }}
    >
      <PageContentBase>
        <Col span={24}>
          <CodeEditor>
            <FullEditor title="Code Editor" />
          </CodeEditor>
        </Col>
      </PageContentBase>
    </Page>
  );
}
